<template>
    <div>
        <v-app-bar color="primary" dark>
            <v-toolbar-title class="text-center white--text" style="width: 400px;">博客管理后台</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-tabs hide-slider color="white">
                <v-tab replace :to="{path:'/admin/article'}">文章</v-tab>
                <v-tab replace :to="{path:'/admin/type'}">分类</v-tab>
                <v-tab replace :to="{path:'/admin/tag'}">标签</v-tab>
            </v-tabs>
            <v-btn icon @click="$vuetify.theme.dark=!$vuetify.theme.dark">
                <v-icon v-if="!$vuetify.theme.dark">mdi-brightness-7</v-icon>
                <v-icon v-else>mdi-brightness-4</v-icon>
            </v-btn>
            <v-menu offset-y open-on-hover nudge-bottom="5">
                <template v-slot:activator="{ on, attrs }">
                    <v-btn text class="align-self-center mr-4" v-bind="attrs" v-on="on">
                        <v-avatar>
                            <img src="../../assets/logo.svg" alt="头像">
                        </v-avatar>
                        苗锦洲
                        <v-icon right>mdi-menu-down</v-icon>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-item v-for="(item, index) in items" :key="index" :to="{path:item.path}">
                        <v-list-item-icon>
                            <v-icon>{{item.icon}}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item>
                    <v-list-item @click="logout">
                        <v-list-item-icon>
                            <v-icon>mdi-logout-variant</v-icon>
                        </v-list-item-icon>
                        <v-list-item-title>退出登录</v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>
        </v-app-bar>
        <keep-alive>
            <router-view/>
        </keep-alive>
    </div>
</template>

<script>
    export default {
        name: "HeaderAdmin",
        data() {
            return {
                items: [
                    {title: '首页', icon: 'mdi-home', path: "/index/home"},
                    {title: '分类', icon: 'mdi-view-list', path: "/index/type"},
                    {title: '标签', icon: 'mdi-tag-multiple', path: "/index/tag"},
                    {title: '归档', icon: 'mdi-timeline-text', path: "/index/timeline"},
                    {title: '关于', icon: 'mdi-information', path: "/index/about"}
                ]
            }
        },
        methods: {
            logout() {
                if (confirm("确定退出登录？")) {
                    this.axios.get(this.API.URL.USER_LOGOUT)
                        .then(value => {
                            let data = value.data
                            if (data.success) {
                                sessionStorage.removeItem("state")
                                alert("退出登录成功")
                                this.$router.replace({path: "/index/home"})
                            } else {
                                alert(data.errorMsg)
                                console.log(value)
                            }
                        }, reason => {
                            console.log(reason)
                        })
                }
            }
        },
        watch: {},
        created: function () {
        }
    }
</script>

<style scoped>

</style>