<template>
    <div>
        <v-navigation-drawer app v-model="drawer" temporary>
            <v-list-item>
                <v-list-item-avatar>
                    <img src="../assets/logo.svg" alt="头像">
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title class="title">OrdinaryRoad</v-list-item-title>
                    <v-list-item-subtitle>平凡之路的个人博客</v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
            <v-divider/>
            <v-list dense nav>
                <v-list-item-group v-model="currentSelectedItem" mandatory color="primary">
                    <v-list-item v-for="item in items" :key="item.title" replace :to="{path:item.path}">
                        <v-list-item-icon>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ item.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>
            </v-list>
            <v-btn style="position: fixed;bottom: 2px;right: 2px" icon
                   @click="$vuetify.theme.dark=!$vuetify.theme.dark">
                <v-icon v-if="!$vuetify.theme.dark">mdi-brightness-7</v-icon>
                <v-icon v-else>mdi-brightness-4</v-icon>
            </v-btn>
        </v-navigation-drawer>
        <v-app-bar app dark elevate-on-scroll shrink-on-scroll
                   :src="imgSrc" :height="$vuetify.breakpoint.smAndDown?200:300">
            <template v-slot:img="{ props }">
                <v-img v-bind="props" gradient="rgba(0,0,0,.1),rgba(0,0,0,.1)"/>
            </template>
            <v-app-bar-nav-icon @click="drawer = !drawer"/>
            <v-toolbar-title class="pa-0 d-flex justify-center align-center white--text"
                             style="height:100%;width: 100%">
                {{title}}
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn icon v-on="on" @click="showSearchToolBar = true">
                        <v-icon>mdi-magnify</v-icon>
                    </v-btn>
                </template>
                <span>搜索</span>
            </v-tooltip>
        </v-app-bar>
        <v-fade-transition>
            <v-overlay v-if="showSearchToolBar" opacity="0.7">
                <v-app-bar app color="primary" dark>
                    <v-text-field prepend-icon="mdi-magnify" hide-details single-line clearable
                                  autofocus/>
                    <v-btn icon @click="showSearchToolBar = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                </v-app-bar>
                <v-content>
                    <v-container>
                        <v-card-text>搜索</v-card-text>
                    </v-container>
                </v-content>
            </v-overlay>
        </v-fade-transition>
    </div>
</template>

<script>
    export default {
        name: "Header",
        data() {
            return {
                items: [
                    {title: '首页', icon: 'mdi-home', path: "/index/home", imgSrc: "/assets/header.jpg"},
                    {title: '分类', icon: 'mdi-view-list', path: "/index/type", imgSrc: "/assets/header.jpg"},
                    {title: '标签', icon: 'mdi-tag-multiple', path: "/index/tag", imgSrc: "/assets/header.jpg"},
                    {
                        title: '归档',
                        icon: 'mdi-timeline-text',
                        path: "/index/timeline",
                        imgSrc: "/assets/header-timeline.jpg"
                    },
                    {title: '关于', icon: 'mdi-information', path: "/index/about", imgSrc: "/assets/header-about.jpg"}
                ],
                title: "",
                imgSrc: "/assets/header.jpg",
                drawer: false,
                showSearchToolBar: false,
                currentSelectedItem: 0
            }
        },
        methods: {
            updateAppBarTitle: function () {
                // let pathname = window.location.href;
                let pathname = this.$route.fullPath;
                // console.log("pathname", pathname)
                // console.log("this.route", this.$route)
                for (let i = 0; i < this.items.length; i++) {
                    let item = this.items[i]
                    if (item.path === pathname) {
                        this.currentSelectedItem = i
                        this.title = item.title
                        break
                    }
                }
                this.$emit("update:transferParamType", this.$route.params.paramType)
                this.$emit("update:transferParamTag", this.$route.params.paramTag)
            }
        },
        watch: {
            showSearchToolBar() {
                if (this.showSearchToolBar) {
                    let cssStr = "overflow-y: hidden;";
                    document.getElementsByTagName('html')[0].style.cssText = cssStr;
                    document.body.style.cssText = cssStr;
                } else {
                    let cssStr = "overflow-y: auto;";
                    document.getElementsByTagName('html')[0].style.cssText = cssStr;
                    document.body.style.cssText = cssStr;
                }
            },
            currentSelectedItem() {
                this.title = this.items[this.currentSelectedItem].title
                this.imgSrc = this.items[this.currentSelectedItem].imgSrc
            }
        },
        created: function () {
            this.updateAppBarTitle()
        }
    }
</script>

<style scoped>

</style>