                    style="width: 100%;background-color: grey">
            <v-container class="pa-0">
                <v-row class="d-flex align-center justify-center">
                    <v-col v-for="item in items" :key="item.path" :cols="$vuetify.breakpoint.smAndDown?6:'auto'">
                        <router-link :to="item.path" class="link text-center" tag="li">
                            {{item.title}}
                        </router-link>
                    </v-col>
                </v-row>
                <v-row class="d-flex align-center justify-center">
                    <v-col class="text-end">
                        <v-tooltip bottom>
                            <template v-slot:activator="{ on }">
                                <v-btn icon dark v-on="on" target="_blank"
                                       href="https://qm.qq.com/cgi-bin/qm/qr?k=-oDiz44PJ-iwgRKZuEkgFgpq5yk1u6ZH&noverify=0">
                                    <v-icon>mdi-qqchat</v-icon>
                                </v-btn>
                            </template>
                            <span>1962247851</span>
                        </v-tooltip>
                    </v-col>
                    <v-col class="text-start">
                        <v-btn icon dark target="_blank" href="https://github.com/1962247851">
                            <v-icon>mdi-github</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>
            </v-container>
            <span class="white--text" id="span"
                  style="position: absolute;bottom: 5px;left: 0;right: 0; text-align: center;width: 100%">世间种种平凡，都不平凡。<br/>©2019-{{new Date().getFullYear()}} 苗锦洲</span>
        </v-parallax>
    </v-footer>
</template>

<script>
    export default {
        name: "Footer",
        data: () => ({
            items: [
                {title: '首页', path: "/index/home"},
                {title: '分类', path: "/index/type"},
                {title: '标签', path: "/index/tag"},
                {title: '归档', path: "/index/timeline"},
                {title: '关于', path: "/index/about"}
            ]
        }),
        methods: {
            click: function (index) {
                alert(index)
            }
        },
        mounted() {
            const script = document.createElement('script')
            script.src = 'https://v1.cnzz.com/z_stat.php?id=1278929725&web_id=1278929725'
            script.language = 'JavaScript'
            document.body.appendChild(script)
            const script1 = document.createElement('script')
            script1.src = 'https://c.cnzz.com/core.php?web_id=1278929725&t=z'
            script1.language = 'JavaScript'
            document.body.appendChild(script1)
            const a = document.createElement('a')
            a.href = 'https://www.cnzz.com/stat/website.php?web_id=1278929725'
            a.text = ' 站长统计'
            a.title = '站长统计'
            a.style.fontSize = "12px"
            a.style.color = "white"
            document.getElementById('span').appendChild(a)
        },
        watch: {
            '$route': {
                handler() {
                    //避免首次获取不到window._czc
                    setTimeout(() => {
                        if (window._czc) {
                            let location = window.location;
                            let contentUrl = location.pathname + location.hash;
                            let refererUrl = '/';
                            // 用于发送某个URL的PV统计请求，
                            window._czc.push(['_trackPageview', contentUrl, refererUrl])
                        }
                    }, 300)
                },
                // 首次进入页面即执行
                immediate: true
            }
        }
    }
</script>

<style scoped>
    .link:hover {
        cursor: pointer;
