<template>
    <v-app id="app">
        <router-view name="header" v-bind:transferParamType.sync="paramType" v-bind:transferParamTag.sync="paramTag"/>
        <v-content>
            <!--name省略表示name='default'-->
            <v-fade-transition>
                <keep-alive exclude="ArticleDetail">
                    <router-view :param-type="paramType" :param-tag="paramTag" v-scroll="handleScroll"/>
                </keep-alive>
            </v-fade-transition>
            <v-fab-transition>
                <v-btn v-if="$route.name!=='article' && fab" fab @click="$vuetify.goTo(0)"
                       fixed bottom right color="accent">
                    <v-icon>mdi-chevron-up</v-icon>
                </v-btn>
            </v-fab-transition>
        </v-content>
        <router-view name="footer"/>
    </v-app>
</template>

<script>
    export default {
        name: 'App',
        components: {},
        props: {
            attrs: {
                type: Object,
                default: () => ({}),
            },
        },
        data: vm => ({
            initialDark: vm.$vuetify
                ? vm.$vuetify.theme.dark
                : false,
            paramType: String,
            paramTag: String,
            fab: false
        }),
        methods: {
            handleScroll(event) {
                this.fab = event.target.scrollingElement.scrollTop !== 0
            }
        },
        beforeDestroy() {
            if (!this.$vuetify) return
            this.$vuetify.theme.dark = this.initialDark
        },
        created() {
            document.title = "平凡之路的个人博客"
        }
    };
</script>
