import Vue from 'vue'
import VueRouter from 'vue-router'
import Header from "@/components/Header"
import Footer from "@/components/Footer"
import About from "@/components/About"
import HeaderAdmin from "@/components/admin/HeaderAdmin"

Vue.use(VueRouter)

export const constantRouters = [
    {
        path: "",
        redirect: "/index"
    },
    {
        path: "/index",
        redirect: "/index/home"
    },
    //放在这是因为不要HeaderAdmin还有Footer
    {
        path: "/admin/login",
        component: () => import("@/components/admin/Login")
    },
    {
        path: "/admin",
        components: {
            default: HeaderAdmin,
            footer: Footer
        },
        children: [
            {
                path: "",
                redirect: "/admin/login"
            },
            {
                path: '/admin/article',
                // redirect: "/admin/article/articleList",
                component: () => import("@/components/admin/article/Article"),
                children: [
                    {
                        path: "",
                        component: About
                    },
                    {
                        path: "/admin/article/newArticle",
                        name: "/admin/article/newArticle",
                        component: () => import("@/components/admin/article/NewArticle"),
                    },
                    {
                        path: "/admin/article/articleList",
                        component: () => import("@/components/admin/article/ArticleList")
                    }
                ]
            },
            {
                path: '/admin/type',
                // redirect: "/admin/type/typeList",
                component: () => import("@/components/admin/type/Type"),
                children: [
                    {
                        path: "",
                        component: About
                    },
                    {
                        path: "/admin/type/newType",
                        component: () => import("@/components/admin/type/NewType")
                    },
                    {
                        path: "/admin/type/typeList",
                        component: () => import("@/components/admin/type/TypeList")
                    }
                ]
            },
            {
                path: '/admin/tag',
                // redirect: "/admin/type/tagList",
                component: () => import("@/components/admin/tag/Tag"),
                children: [
                    {
                        path: "",
                        component: About
                    },
                    {
                        path: "/admin/tag/newTag",
                        component: () => import("@/components/admin/tag/NewTag")
                    },
                    {
                        path: "/admin/tag/tagList",
                        component: () => import("@/components/admin/tag/TagList")
                    }
                ]
            }
        ]
    },
    {
        path: '/index/home',
        components: {
            header: Header,
            default: () => import("@/components/Home"),
            footer: Footer
        }
    },
    {
        path: '/index/type',
        name: '/index/type',
        components: {
            header: Header,
            default: () => import("@/components/Type"),
            footer: Footer
        }
    },
    {
        path: '/index/tag',
        name: '/index/tag',
        components: {
            header: Header,
            default: () => import("@/components/Tag"),
            footer: Footer
        }
    },
    {
        path: '/index/timeline',
        components: {
            header: Header,
            default: () => import("@/components/Timeline"),
            footer: Footer
        }
    },
    {
        path: '/index/about',
        components: {
            header: Header,
            default: About,
            footer: Footer
        }
    },
    {
        name: 'article',
        path: '/article/:id.html',
        component: () => import("@/components/ArticleDetail"),
        props: true
    },
    {
        path: "*",
        component: () => import("@/components/NotFound"),
    },
    {
        path: "/404",
        name: "404",
        component: () => import("@/components/NotFound"),
    }
]


export default new VueRouter({
    routes: constantRouters,
    scrollBehavior(to, from, saveTop) {
        if (saveTop) {
            return saveTop;
        } else {
            return {x: 0, y: 0}
        }
    }
    // scrollBehavior: (to, from, savedPosition) => {
    //     let scrollTo = 0
    //     if (to.hash) {
    //         scrollTo = to.hash
    //     } else if (savedPosition) {
    //         scrollTo = savedPosition.y
    //     }
    //     return goTo(scrollTo)
    // },
})